<template>
  <div>
    <div style='background:#e4e9ee;height:30px;'> 13012345678</div>
    <div class="pb-10" style='background:#e4e9ee'>
      <el-row :gutter="10">
        <el-col :span="7">
          <div class="panel-bai">
            <div class="cate-item">
              <div class="t-center cate-item-icon" style='background:#A7E9E5'>
                <i style='font-size:24px;color:#888;' class="iconfont icon-bussinessman"></i>
              </div>
              <div class="cate-item-content">
                <div>1123123123</div>
                <div>123123123</div>
              </div>
            </div>
          </div>
        </el-col>

        <el-col :span="7">
          <div class="panel-bai">
            <div class="cate-item">
                <div class="t-center cate-item-icon" style='background:#FFF49D'>
                  <i style='font-size:24px;color:#888;' class="iconfont icon-creditlevel"></i>
                </div>
                <div class="cate-item-content">
                  <div></div>
                  <div><span class='t-12'>2017-11-11  2018-11-11</span></div>
                </div>
            </div>
          </div>
        </el-col>

        <el-col :span="5">
          <div class="panel-bai">
            <div class="cate-item">
              <div class="t-center cate-item-icon" style='background:#50E3C2'>
                <i style='font-size:24px;color:#888;' class="iconfont icon-dollar"></i>
              </div>
              <div class="cate-item-content">
                <div>0</div>
                <div>0</div>
              </div>
            </div>
          </div>
        </el-col>

        <el-col :span="5">
          <div class="panel-bai">
            <div class="cate-item">
              <div class="t-center cate-item-icon" style='background:#7ED321'>
                <i style='font-size:24px;color:#888;' class="iconfont icon-form"></i>
              </div>
              <div class="cate-item-content">
                <div>11</div>
                <div>12</div>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="pb-10" style='background:#e4e9ee'>
      <div class="panel-bai">
          <div class="t-bold mb-5"></div>
          <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column
              prop="date"
              label=""
              width="180">
            </el-table-column>
            <el-table-column
              prop="name"
              label=""
              width="180">
            </el-table-column>
            <el-table-column
              prop="address"
              label="">
            </el-table-column>
          </el-table>
      </div>
    </div>
    <!-- <div style='height:2000px;'></div> -->
    <div class="pb-20">
      <div class="panel-bai">
        <el-row>
          <el-col :span="12">
            <div class="t-bold mb-5"></div>
            <div ref='pie' class='chart-panel'></div>
          </el-col>
          <el-col :span="12">
            <div class="t-bold mb-5"></div>
            <div ref='bar' class='chart-panel'></div>
          </el-col>
        </el-row>
      </div>
    </div>
    
  </div>
</template>

<script>
const { echarts } = require("~/assets/scripts/echarts.common.min.js");
let charts = {};
export default {
  data() {
    return {
      tableData: [
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-04",
          name: "",
          address: " 1517 "
        },
        {
          date: "2016-05-01",
          name: "",
          address: " 1519 "
        },
        {
          date: "2016-05-03",
          name: "",
          address: " 1516 "
        }
      ],
      account_statistics: [
        { value: 335, name: "" },
        { value: 310, name: "" },
        { value: 234, name: "" },
        { value: 135, name: "" },
        { value: 1548, name: "" }
      ],
      account_statistics_base: [
        "",
        "",
        "",
        "",
        ""
      ],
      recent_7_calls: {
        title: [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "D",
          "E",
          "M",
          "O",
          ",",
          "",
          "",
          "",
          "",
          ""
        ],
        data: [
          220,
          182,
          191,
          234,
          290,
          330,
          310,
          123,
          442,
          321,
          90,
          149,
          210,
          122,
          133,
          334,
          198,
          123,
          125,
          220
        ]
      }
    };
  },
  methods: {
    getRows() {
      // this.getData().then(res => {
      this.getPie();
      this.getBar();
      // });
    },
    getPie() {
      // 
      var option = {
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "left",
          //top:'middle',
          data: ["A", "B", "C", "D", "E"],
          formatter: name => {
            //console.log(name,this.account_statistics_base);
            return `${name} ${this.account_statistics_base[name]}`;
          }
        },
        series: [
          {
            name: "",
            type: "pie",
            //radius: "55%",
            //center: ["50%", "50%"],
            data: this.account_statistics,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };

      // 
      charts.pie.setOption(option);
    },
    getBar() {
      // 
      var option = {
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c}"
        },
        legend: {
          data: this.recent_7_calls.title
        },
        xAxis: {
          data: this.recent_7_calls.title
        },
        yAxis: {},
        series: [
          {
            type: "bar",
            data: this.recent_7_calls.data,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#83bff6" },
                  { offset: 0.5, color: "#188df0" },
                  { offset: 1, color: "#188df0" }
                ])
              },
              emphasis: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#2378f7" },
                  { offset: 0.7, color: "#2378f7" },
                  { offset: 1, color: "#83bff6" }
                ])
              }
            }
          }
        ]
      };

      // 
      charts.bar.setOption(option);
    }
  },
  mounted() {
    charts = {
      pie: echarts.init(this.$refs.pie),
      bar: echarts.init(this.$refs.bar)
    };
    this.getRows();
  }
};
</script>

<style lang="less" scoped>
.cate-item {
  padding-left: 80px;
  overflow: hidden;
}

.cate-item-content {
  line-height: 30px;
}
.cate-item-icon {
  float: left;
  width: 60px;
  height: 60px;
  line-height: 60px;
  border-radius: 60px;
  margin-left: -80px;
}
.chart-panel {
  height: 400px;
  width: 500px;
}
</style>




